import { Component, OnInit } from '@angular/core';
import { IUser } from 'src/app/core/user.model';
import { AuthUserService } from 'src/app/core/auth-user.service';
import { MenuService } from 'src/app/core/menu.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  sidebarOpen$ = this.menuService.sidebarOpen$;
  user: IUser;
  profileDropDown = false;
  constructor(private authUserService: AuthUserService, private menuService: MenuService, private router: Router) { }

  ngOnInit(): void {
    console.log('Loaded header');

    this.user = this.authUserService.user;
  }

  toggleSidebar(): void {
    const currentValue = this.menuService.sidebarOpen$.getValue();
    this.menuService.sidebarOpen$.next(!currentValue);
  }

  logout(): void {
    console.log('Logging out');
    this.authUserService.logout();
  }

  goToProfile(): void {
    if (this.user && this.user.role == 'Vendor') {
      this.router.navigate(['vendor', 'detail', this.user._id]);
    }
  }
}
